body, html {
  height: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  &.hidden {
    display: block;
    position: relative;
    min-height: 100vh;
    -webkit-overflow-scrolling: touch;
    overflow: hidden;
    @media (min-width: 1050px) {
      padding-right: 17px;
      & header {padding-right: 17px;}
    }
  }
}


.main {
  flex: 1 0 auto;
}
.footer {
  flex: 0 0 auto;
}

.wrapper {
    width: 100%;
    max-width: 1390px;
    padding: 0 20px;
    margin: 0 auto;
  }
.wrapper-s {
  max-width: 1182px;
}
.wrapper-m {
  max-width: 1270px;
}
.wrapper-l {
  max-width: 1370px;
}
.wrapper-xl {
  max-width: 1520px;
}
.title {
  padding-left: 175px;
  font-family: "Circe-ExtraLight";
  font-size: 52px;
  line-height: 52px;
  text-transform: uppercase;
  color: #06828a;
  @media (max-width: 1250px) {
    padding-left: 0;
    font-size: 39px;
    line-height: 39px;
  }
  @media (max-width: 992px) {
    font-size: 26px;
    line-height: 26px;
  }
}
.form-field {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  max-width: 208px;
  height: 35px;
  padding: 10px 13px 8px;
  background-color: none;
  font-family: "Century Gothic Regular";
  font-size: 16px;
  line-height: 18px;
  color: #939393;
  background: none;
  border: 1px solid #c2c0b3;
  transition: 0.3s ease;

  &:not(textarea) {
    resize: none;
    white-space: nowrap;
    overflow-x: hidden;
  }
  
  @media (max-width: 1100px) {
    max-width: 170px;
  }
  @media (max-width: 650px) {max-width: 220px;}
  &.error {
    border: 1px solid red;
  }
  &:focus {
    border: 1px solid #ffffff;
  }
}
.form-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  max-width: 220px;
  height: 35px;
  padding: 0 10px 0 23px;
  background: none;
  font-family: "Circe-ExtraLight";
  font-size: 16px;
  line-height: 35px;
  text-transform: uppercase;
  color: #939393;
  border: 1px solid #c2c0b3;
  transition: 0.3s ease;
  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 0.5px);
    right: 33px;
    display: block;
    width: 30px;
    height: 1px;
    background-color: #c2c0b3;
    transition: 0.3s ease;
  }
  &:hover::before {width: calc(100% - 68px);}
}